body {
  margin: 0;
  height: 100vh;
}
#root {
  width: 100%;
  height: 100%;
}
.top-pane {
  background-color: hsl(
    225,
    6%,
    25%
  );
}
.pane {
  height: 50vh;
  display: flex;
}
@media (max-width: 750px) {
  .pane {
    height: 140vh;
    display: flex;
    flex-direction: column;
  }
}
.editor-container {
  flex-grow: 1;
  flex-basis: 0;
  display: flex;
  flex-direction: column;
  padding: 0.5rem;
  background-color: hsl(
    225,
    6%,
    25%
  );
}

.editor-container.collapsed {
  flex-grow: 0;
}

.editor-container.collapsed
  .CodeMirror-scroll {
  position: absolute;
  overflow: hidden !important;
}

.expand-collapse-btn {
  margin-left: 0.5rem;
  background: none;
  border: none;
  color: white;
  cursor: pointer;
}

.editor-title {
  display: flex;
  justify-content: space-between;
  background-color: hsl(
    225,
    6%,
    13%
  );
  color: white;
  padding: 0.5rem 0.5rem
    0.5rem 1rem;
  border-top-right-radius: 0.5rem;
  border-top-left-radius: 0.5rem;
}

.CodeMirror {
  height: 100% !important;
}

.code-mirror-wrapper {
  flex-grow: 1;
  border-bottom-right-radius: 0.5rem;
  border-bottom-left-radius: 0.5rem;
  overflow: hidden;
}
